.textarea-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 20px 0;
}

.textarea-section {
    display: flex;
    flex-direction: column;
}

.textarea-label {
    font-size: 1rem;
    margin-bottom: 5px;
}

textarea {
    width: 100%;
    height: 100px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    font-family: 'Courier New', Courier, monospace;
}

textarea.textarea-readonly {
    background-color: #f4f4f4;
    cursor: not-allowed;
}

.button-container {
    display: flex;
    gap: 10px;
}

button {
    padding: 10px 15px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

button:hover {
    background-color: #0056b3;
}

.results {
    margin-top: 20px;
}